<template>
  <div id="app">
    <img src="./assets/develom_logo.png">
    <h1> {{title}} </h1>
    <div id="menu">
      <div id="left">
        <div id="left-cont" @mouseover="newsOver" @mouseout="newsOut">
          <p id="news">News</p>
          <div id="arrow-left">
            <img class="icons arrow" id="arrow1" src="./assets/icons/arrow-right.png">
          </div>
        </div>
      </div>
      <div id="right">
        <div id="right-cont" @mouseover="blogOver" @mouseout="blogOut">
          <p id="blog">Blog</p>
          <div id="arrow-right">
            <img class="icons arrow" id="arrow2" src="./assets/icons/arrow-right.png">
          </div>
        </div>
      </div>
    </div>
    <router-view/>
  </div>
</template>

<script>
import { TweenLite, Sine } from 'gsap'
export default {
  name: 'App',
  data () {
    return {
      title: "Hi! Develom's Here"
    }
  },
  methods: {
    newsOver: function () {
      TweenLite.to('#news', 0.5, { x: '-16' })
      TweenLite.to('#arrow1', 0.6, { x: '27', ease: Sine.easeOut })
    },
    newsOut: function () {
      TweenLite.to('#news', 0.5, { x: '0' })
      TweenLite.to('#arrow1', 0.4, { x: '0', ease: Sine.easeOut })
    },
    blogOver: function () {
      TweenLite.to('#blog', 0.5, { x: '-16' })
      TweenLite.to('#arrow2', 0.6, { x: '27', ease: Sine.easeOut })
    },
    blogOut: function () {
      TweenLite.to('#blog', 0.5, { x: '0' })
      TweenLite.to('#arrow2', 0.4, { x: '0', ease: Sine.easeOut })
    }
  }
}
</script>

<style lang="scss">
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 200px;
}

#menu {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  font-size: 25px;
  width: 100%;
  height: 80px;
}

#left {
  border: black 1px solid;
  width: 120px;
  margin-right: 15px;
  cursor: pointer;

  #left-cont {
    position: absolute;
    width: 120px;
    height: 79px;
    overflow: hidden;
  }

  p {
    position: absolute;
    left: 29px;
    background-color: #fff;
    border-radius: 10px;
    z-index: 2;
  }

  #arrow-left {
    position: relative;
    overflow: hidden;
    top: 27px;
    left: 80px;
    width: 40px;

    #arrow1 {
      position: relative;
      left: -35px;
    }
  }

}

#right {
  border: black 1px solid;
  width: 120px;
  margin-left: 15px;
  cursor: pointer;

  #right-cont {
    position: absolute;
    width: 120px;
    height: 79px;
    overflow: hidden;
  }

  p {
    position: absolute;
    left: 36px;
    background-color: #fff;
    border-radius: 10px;
    z-index: 2;
  }

  #arrow-right {
    position: relative;
    overflow: hidden;
    top: 27px;
    left: 74px;
    width: 40px;

    #arrow2 {
      position: relative;
      left: -35px;
    }
  }

}

</style>



